'use client';
import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './First_section.module.css';

gsap.registerPlugin(ScrollTrigger);

const ExpandingSection = () => {
  const wrapperRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const ctx = gsap.context(() => {
      if (!wrapperRef.current || !contentRef.current) return;

      const timeline = gsap.timeline({
        scrollTrigger: {
          trigger: wrapperRef.current,
          start: 'top bottom', // starts when bottom of hero touches top
          end: '+=100%',
          scrub: true,
          pin: true,
        },
      });

      timeline.fromTo(
        contentRef.current,
        {
          scale: 0.3,
          borderRadius: '20px',
        },
        {
          scale: 1,
          borderRadius: '0px',
          ease: 'power2.inOut',
        }
      );
    });

    return () => ctx.revert();
  }, []);

  return (
    <section ref={wrapperRef} className={styles.expandingWrapper}>
      <div ref={contentRef} className={styles.innerBox}>
        <h2>EXPANDING SECTION</h2>
        <p>This content grows to full screen as you scroll.</p>
      </div>
    </section>
  );
};

export default ExpandingSection;
